import "core-js/modules/es.array.push.js";
import "core-js/modules/es.array-buffer.detached.js";
import "core-js/modules/es.array-buffer.transfer.js";
import "core-js/modules/es.array-buffer.transfer-to-fixed-length.js";
import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.filter.js";
import "core-js/modules/es.iterator.find.js";
import "core-js/modules/es.iterator.for-each.js";
import "core-js/modules/es.iterator.map.js";
import "core-js/modules/es.iterator.reduce.js";
import "core-js/modules/es.json.stringify.js";
import "core-js/modules/es.typed-array.with.js";
import "core-js/modules/esnext.json.parse.js";
import "core-js/modules/esnext.uint8-array.set-from-base64.js";
import "core-js/modules/esnext.uint8-array.set-from-hex.js";
import "core-js/modules/esnext.uint8-array.to-base64.js";
import "core-js/modules/esnext.uint8-array.to-hex.js";
import "core-js/modules/web.dom-exception.stack.js";
import "core-js/modules/web.url-search-params.delete.js";
import "core-js/modules/web.url-search-params.has.js";
import "core-js/modules/web.url-search-params.size.js";
import axios from '@/utils/axios';
import * as imageConversion from 'image-conversion';
import { mapGetters } from 'vuex';
export default {
  name: 'UploadForm',
  props: {
    selectedUrlForm: {
      type: String,
      default: 'url',
      required: false
    },
    customerCompress: {
      type: Boolean,
      default: true,
      required: false
    },
    compressQuality: {
      type: Number,
      default: 4,
      required: false
    },
    compressBar: {
      type: Number,
      default: 5,
      required: false
    },
    serverCompress: {
      type: Boolean,
      default: true,
      required: false
    },
    uploadChannel: {
      type: String,
      default: 'telegram',
      required: false
    },
    uploadNameType: {
      type: String,
      default: 'default',
      required: false
    },
    useCustomUrl: {
      type: String,
      default: 'false',
      required: false
    },
    customUrlPrefix: {
      type: String,
      default: '',
      required: false
    },
    autoRetry: {
      type: Boolean,
      default: true,
      required: false
    },
    urlPrefix: {
      type: String,
      default: '',
      required: false
    },
    uploadMethod: {
      type: String,
      default: 'default',
      required: false
    },
    uploadFolder: {
      type: String,
      default: '',
      required: false
    }
  },
  data() {
    return {
      fileList: [],
      uploading: false,
      maxUploading: 6,
      exceptionList: [],
      listScrolled: false,
      fileListLength: 0,
      uploadCount: 0,
      pastedUrls: '',
      pasteUploadMethod: 'save',
      // 
      autoReUpload: true,
      maxRetryCount: 10,
      // 
      retryTimer: null,
      // 
      retryDelay: 12000,
      // 
      // 
      uploadQueue: [],
      // 
      activeUploads: 0,
      // 
      maxConcurrentUploads: 6,
      // 
      // 
      abortControllers: new Map() //  AbortController
    };
  },
  watch: {
    fileList: {
      handler() {
        if (this.fileList.length > this.fileListLength) {
          this.$nextTick(() => {
            setTimeout(() => {
              if (this.$refs.scrollContainer) {
                // this.$refs.scrollContainer.setScrollTop(this.$refs.scrollContainer.wrapRef.scrollHeight) // 
                this.$refs.scrollContainer.setScrollTop(0); // 
              }
            }, 100);
          });
        }
        this.fileListLength = this.fileList.length;
      },
      deep: true
    },
    useCustomUrl: {
      handler() {
        if (this.useCustomUrl === 'true') {
          this.fileList.forEach(item => {
            if (item.uploadChannel === 'external') {
              return;
            }
            item.finalURL = this.customUrlPrefix + item.srcID;
            item.mdURL = `![${item.name}](${this.customUrlPrefix + item.srcID})`;
            item.htmlURL = `<img src="${this.customUrlPrefix + item.srcID}" alt="${item.name}" width=100% />`;
            item.ubbURL = `[img]${this.customUrlPrefix + item.srcID}[/img]`;
          });
        } else {
          this.fileList.forEach(item => {
            if (item.uploadChannel === 'external') {
              return;
            }
            item.finalURL = this.rootUrl + item.srcID;
            item.mdURL = `![${item.name}](${this.rootUrl + item.srcID})`;
            item.htmlURL = `<img src="${this.rootUrl + item.srcID}" alt="${item.name}" width=100% />`;
            item.ubbURL = `[img]${this.rootUrl + item.srcID}[/img]`;
          });
        }
      },
      immediate: true
    },
    customUrlPrefix: {
      handler() {
        if (this.useCustomUrl === 'true') {
          this.fileList.forEach(item => {
            if (item.uploadChannel === 'external') {
              return;
            }
            item.finalURL = this.customUrlPrefix + item.srcID;
            item.mdURL = `![${item.name}](${this.customUrlPrefix + item.srcID})`;
            item.htmlURL = `<img src="${this.customUrlPrefix + item.srcID}" alt="${item.name}" width=100% />`;
            item.ubbURL = `[img]${this.customUrlPrefix + item.srcID}[/img]`;
          });
        }
      },
      immediate: true
    },
    autoReUpload(val) {
      this.$store.commit('setStoreAutoReUpload', val);
    }
  },
  computed: {
    ...mapGetters(['storeAutoReUpload']),
    uploadSuccessCount() {
      return this.fileList.filter(item => item.status === 'done' || item.status === 'success').length;
    },
    uploadErrorCount() {
      return this.fileList.filter(item => item.status === 'exception').length;
    },
    uploadingCount() {
      return this.fileList.filter(item => item.status === 'uploading').length;
    },
    waitingCount() {
      return this.uploadQueue.length;
    },
    urlSize() {
      // small
      return window.innerWidth < 768 ? 'small' : 'default';
    },
    disableTooltip() {
      return window.innerWidth < 768;
    },
    rootUrl() {
      //  > urlPrefix > 
      return this.useCustomUrl === 'true' ? this.customUrlPrefix : this.urlPrefix || `${window.location.protocol}//${window.location.host}/file/`;
    },
    pasteCardMethodButtonSize() {
      if (this.fileList.length) {
        return 'small';
      } else {
        return window.innerWidth < 768 ? 'small' : 'medium';
      }
    }
  },
  mounted() {
    document.addEventListener('paste', this.handlePaste);
    this.autoReUpload = this.storeAutoReUpload;
  },
  beforeUnmount() {
    document.removeEventListener('paste', this.handlePaste);
    // 
    this.uploadQueue = [];
    this.fileList = [];
    this.activeUploads = 0;
  },
  methods: {
    // 
    truncateFilename(filename, maxLength = 20) {
      if (!filename || filename.length <= maxLength) {
        return filename;
      }
      const lastDotIndex = filename.lastIndexOf('.');
      let name, ext;
      if (lastDotIndex > 0) {
        name = filename.substring(0, lastDotIndex);
        ext = filename.substring(lastDotIndex);
      } else {
        name = filename;
        ext = '';
      }
      // 
      const keepEnd = ext.length + 4; //  + 4
      const keepStart = maxLength - keepEnd - 3; // 3...
      if (keepStart <= 0) {
        return filename.substring(0, maxLength - 3) + '...';
      }
      return name.substring(0, keepStart) + '...' + name.slice(-4) + ext;
    },
    uploadFile(file) {
      // fileList
      if (!this.fileList.find(item => item.uid === file.file.uid)) {
        return;
      }

      // 
      if (this.activeUploads >= this.maxConcurrentUploads) {
        this.uploadQueue.push(file);
        this.fileList.find(item => item.uid === file.file.uid).status = 'waiting';
        return;
      }

      // 
      this.activeUploads++;
      this.fileList.find(item => item.uid === file.file.uid).status = 'uploading';
      const uploadChannel = this.fileList.find(item => item.uid === file.file.uid).uploadChannel || this.uploadChannel;

      // 
      if (uploadChannel === 'external') {
        this.uploadSingleFile(file);
        return;
      }

      // HuggingFace 
      // <20MB CF Workers 
      // >=20MB HuggingFace S3 CF Workers 
      if (uploadChannel === 'huggingface') {
        const HF_DIRECT_THRESHOLD = 20 * 1024 * 1024; // 20MB
        if (file.file.size >= HF_DIRECT_THRESHOLD) {
          this.uploadToHuggingFaceDirect(file);
        } else {
          this.uploadSingleFile(file);
        }
        return;
      }

      // Discord  10MB 9MB 
      if (uploadChannel === 'discord') {
        const DISCORD_CHUNK_THRESHOLD = 9 * 1024 * 1024; // 9MB
        if (file.file.size > DISCORD_CHUNK_THRESHOLD) {
          this.uploadFileInChunks(file);
        } else {
          this.uploadSingleFile(file);
        }
        return;
      }

      // 
      const CHUNK_THRESHOLD = 20 * 1024 * 1024; // 20MB
      if (file.file.size > CHUNK_THRESHOLD) {
        this.uploadFileInChunks(file);
      } else {
        this.uploadSingleFile(file);
      }
    },
    // 
    processUploadQueue() {
      // 
      if (this.uploadQueue.length === 0 || this.activeUploads >= this.maxConcurrentUploads) {
        return;
      }

      // 
      const nextFile = this.uploadQueue.shift();
      if (nextFile && this.fileList.find(item => item.uid === nextFile.file.uid)) {
        this.uploadFile(nextFile);
      } else {
        // 
        this.processUploadQueue();
      }
    },
    // 
    onUploadComplete() {
      this.activeUploads = Math.max(0, this.activeUploads - 1);

      // 
      this.processUploadQueue();

      // 
      if (this.activeUploads === 0 && this.uploadQueue.length === 0) {
        this.uploading = false;
      }
    },
    // 
    async uploadSingleFile(file) {
      const fileItem = this.fileList.find(item => item.uid === file.file.uid);
      if (!fileItem) return; // 

      const needServerCompress = fileItem.serverCompress;
      const uploadChannel = fileItem.uploadChannel || this.uploadChannel;
      const autoRetry = this.autoRetry && uploadChannel !== 'external';
      const uploadNameType = uploadChannel === 'external' ? 'default' : this.uploadNameType;

      //  AbortController 
      const abortController = new AbortController();
      this.abortControllers.set(file.file.uid, abortController);
      const formData = new FormData();
      formData.append('file', file.file);
      if (uploadChannel === 'external') {
        formData.append('url', file.file.url);
      }

      // HuggingFace  SHA256 CPU 
      if (uploadChannel === 'huggingface') {
        try {
          console.log('Computing SHA256 for HuggingFace upload...');
          const sha256 = await this.computeSha256(file.file);
          formData.append('sha256', sha256);
          console.log('SHA256 computed:', sha256);
        } catch (err) {
          console.error('Failed to compute SHA256:', err);
          // 
        }
      }
      axios({
        url: '/upload' + '?serverCompress=' + needServerCompress + '&uploadChannel=' + uploadChannel + '&uploadNameType=' + uploadNameType + '&autoRetry=' + autoRetry + '&uploadFolder=' + this.uploadFolder,
        method: 'post',
        data: formData,
        withAuthCode: true,
        signal: abortController.signal,
        // 
        onUploadProgress: progressEvent => {
          const percentCompleted = Math.round(progressEvent.loaded / progressEvent.total * 100);
          file.onProgress({
            percent: percentCompleted,
            file: file.file
          });
        }
      }).then(res => {
        file.onSuccess(res, file.file);
      }).catch(err => {
        // 
        if (err.name === 'CanceledError' || err.code === 'ERR_CANCELED') {
          console.log(':', file.file.name);
          return;
        }
        if (err.response && err.response.status !== 401) {
          this.exceptionList.push(file);
          file.onError(err, file.file);
        } else if (!err.response) {
          //  ERR_HTTP2_PROTOCOL_ERROR
          this.exceptionList.push(file);
          file.onError(err, file.file);
        }
      }).finally(() => {
        //  AbortController
        this.abortControllers.delete(file.file.uid);
        // 
        this.onUploadComplete();
      });
    },
    // 
    async uploadFileInChunks(file) {
      const fileItem = this.fileList.find(item => item.uid === file.file.uid);
      if (!fileItem) return; // 

      const uploadChannel = fileItem.uploadChannel || this.uploadChannel;

      //  AbortController 
      const abortController = new AbortController();
      this.abortControllers.set(file.file.uid, abortController);

      // Discord  9MB Discord  10MB
      // Telegram  16MB TG Bot getFile  20MB 4MB 
      //  16MB 
      const CHUNK_SIZE = uploadChannel === 'discord' ? 9 * 1024 * 1024 // 9MB for Discord
      : 16 * 1024 * 1024; // 16MB for Telegram and others (TG getFile limit: 20MB)

      const fileSize = file.file.size;
      const totalChunks = Math.ceil(fileSize / CHUNK_SIZE);
      const needServerCompress = fileItem.serverCompress;
      const autoRetry = this.autoRetry && uploadChannel !== 'external';
      const uploadNameType = uploadChannel === 'external' ? 'default' : this.uploadNameType;

      // HuggingFace  SHA256
      let precomputedSha256 = null;
      if (uploadChannel === 'huggingface') {
        try {
          console.log('Computing SHA256 for HuggingFace chunked upload...');
          precomputedSha256 = await this.computeSha256(file.file);
          console.log('SHA256 computed:', precomputedSha256);
        } catch (err) {
          console.error('Failed to compute SHA256:', err);
        }
      }
      try {
        // uploadId
        const initFormData = new FormData();
        initFormData.append('originalFileName', file.file.name);
        initFormData.append('originalFileType', file.file.type);
        initFormData.append('totalChunks', totalChunks.toString());
        const initResponse = await axios({
          url: '/upload' + '?serverCompress=' + needServerCompress + '&uploadChannel=' + uploadChannel + '&uploadNameType=' + uploadNameType + '&autoRetry=' + autoRetry + '&uploadFolder=' + this.uploadFolder + '&initChunked=true',
          method: 'post',
          data: initFormData,
          withAuthCode: true
        });
        if (!initResponse.data.success) {
          throw new Error(': ' + initResponse.data.message);
        }
        const uploadId = initResponse.data.uploadId;
        console.log('uploadId:', uploadId);

        //  totalChunks  uploadId 
        const fileItem = this.fileList.find(item => item.uid === file.file.uid);
        if (fileItem) {
          fileItem.totalChunks = totalChunks;
          fileItem.uploadId = uploadId;
        }

        // 
        const maxConcurrency = 3; // 
        const chunkProgress = new Array(totalChunks).fill(0);
        let nextChunkIndex = 0;
        let hasError = false;
        let errorMsg = '';
        let isCancelled = false; // 

        const uploadChunk = async chunkIndex => {
          if (hasError || isCancelled) return;
          const start = chunkIndex * CHUNK_SIZE;
          const end = Math.min(start + CHUNK_SIZE, fileSize);
          const chunk = file.file.slice(start, end);
          const formData = new FormData();
          formData.append('file', chunk, `${file.file.name}.part${chunkIndex.toString().padStart(3, '0')}`);
          formData.append('chunkIndex', chunkIndex.toString());
          formData.append('totalChunks', totalChunks.toString());
          formData.append('uploadId', uploadId);
          formData.append('originalFileName', file.file.name);
          formData.append('originalFileType', file.file.type);
          let retryCount = 0;
          const maxRetries = 3;
          while (retryCount < maxRetries) {
            if (hasError || isCancelled) return;
            try {
              await axios({
                url: '/upload' + '?serverCompress=' + needServerCompress + '&uploadChannel=' + uploadChannel + '&uploadNameType=' + uploadNameType + '&autoRetry=' + autoRetry + '&uploadFolder=' + this.uploadFolder + '&chunked=true',
                method: 'post',
                data: formData,
                withAuthCode: true,
                signal: abortController.signal,
                // 
                onUploadProgress: progressEvent => {
                  if (hasError || isCancelled) return;
                  const percent = Math.round(progressEvent.loaded / progressEvent.total * 100);
                  chunkProgress[chunkIndex] = percent;
                  const totalPercent = Math.round(chunkProgress.reduce((a, b) => a + b, 0) / totalChunks);
                  file.onProgress({
                    percent: totalPercent,
                    file: file.file
                  });
                }
              });
              return; // Success
            } catch (err) {
              // 
              if (err.name === 'CanceledError' || err.code === 'ERR_CANCELED') {
                isCancelled = true;
                console.log(':', file.file.name);
                return;
              }
              retryCount++;
              console.warn(` ${chunkIndex + 1}/${totalChunks}  ( ${retryCount}/${maxRetries}):`, err);
              if (retryCount >= maxRetries) {
                hasError = true;
                errorMsg = ` ${chunkIndex + 1}/${totalChunks} : ${err.message}`;
                throw new Error(errorMsg);
              }
              // 
              await new Promise(resolve => setTimeout(resolve, 2000 * retryCount));
            }
          }
        };

        // 
        const pool = [];
        for (let i = 0; i < maxConcurrency; i++) {
          pool.push((async () => {
            while (nextChunkIndex < totalChunks && !hasError && !isCancelled) {
              const currentIndex = nextChunkIndex++;
              try {
                await uploadChunk(currentIndex);
              } catch (e) {
                if (!isCancelled) {
                  hasError = true;
                  errorMsg = e.message;
                }
                break;
              }
            }
          })());
        }
        await Promise.all(pool);

        // 
        if (isCancelled) {
          console.log('');
          return;
        }
        if (hasError) {
          throw new Error(errorMsg || '');
        }

        // 
        this.$message({
          type: 'info',
          message: '...',
          duration: 0 // 
        });
        const mergeFormData = new FormData();
        mergeFormData.append('uploadId', uploadId);
        mergeFormData.append('totalChunks', totalChunks.toString());
        mergeFormData.append('originalFileName', file.file.name);
        mergeFormData.append('originalFileType', file.file.type);
        // HuggingFace  SHA256
        if (precomputedSha256) {
          mergeFormData.append('sha256', precomputedSha256);
        }
        const response = await axios({
          url: '/upload' + '?serverCompress=' + needServerCompress + '&uploadChannel=' + uploadChannel + '&uploadNameType=' + uploadNameType + '&autoRetry=' + autoRetry + '&uploadFolder=' + this.uploadFolder + '&chunked=true&merge=true',
          method: 'post',
          data: mergeFormData,
          withAuthCode: true
        });

        // 
        this.$message.closeAll();

        // 
        file.onSuccess(response, file.file);
      } catch (err) {
        // 
        if (err.name === 'CanceledError' || err.code === 'ERR_CANCELED') {
          console.log(':', file.file.name);
          return;
        }
        console.error(':', err);

        // uploadId
        const fileItem = this.fileList.find(item => item.uid === file.file.uid);
        if (fileItem && fileItem.uploadId) {
          this.cleanupUploadResources(fileItem.uploadId, fileItem.totalChunks).then(() => {
            console.log(`: ${fileItem.uploadId}`);
          }).catch(cleanupError => {
            console.warn(':', cleanupError);
          });
        }
        if (err.response && err.response.status !== 401) {
          this.exceptionList.push(file);
          file.onError(err, file.file);
        } else if (!err.response) {
          // 
          this.exceptionList.push(file);
          file.onError(err, file.file);
        }
      } finally {
        //  AbortController
        this.abortControllers.delete(file.file.uid);
        // 
        this.onUploadComplete();
      }
    },
    handleRemove(file) {
      // 
      if (this.abortControllers.has(file.uid)) {
        this.abortControllers.get(file.uid).abort();
        this.abortControllers.delete(file.uid);
      }
      // 
      this.uploadQueue = this.uploadQueue.filter(item => item.file.uid !== file.uid);
      // 
      this.fileList = this.fileList.filter(item => item.uid !== file.uid);
      this.$message({
        type: 'info',
        message: this.truncateFilename(file.name) + ''
      });
    },
    // 
    async cleanupUploadResources(uploadId, totalChunks = 0) {
      try {
        await axios({
          url: `/upload?cleanup=true&uploadId=${uploadId}&totalChunks=${totalChunks}`,
          method: 'get',
          withAuthCode: true,
          timeout: 5000
        });
        console.log(`: ${uploadId}`);
      } catch (error) {
        console.warn(':', error);
      }
    },
    handleSuccess(response, file) {
      try {
        // 
        const uploadChannel = this.fileList.find(item => item.uid === file.uid).uploadChannel || this.uploadChannel;
        if (uploadChannel !== 'external') {
          // response.data[0].src/file/
          const srcID = response.data[0].src.replace('/file/', '');
          this.fileList.find(item => item.uid === file.uid).url = `${window.location.protocol}//${window.location.host}/file/` + srcID;
          this.fileList.find(item => item.uid === file.uid).finalURL = this.rootUrl + srcID;
          this.fileList.find(item => item.uid === file.uid).mdURL = `![${file.name}](${this.rootUrl + srcID})`;
          this.fileList.find(item => item.uid === file.uid).htmlURL = `<img src="${this.rootUrl + srcID}" alt="${file.name}" width=100% />`;
          this.fileList.find(item => item.uid === file.uid).ubbURL = `[img]${this.rootUrl + srcID}[/img]`;
          this.fileList.find(item => item.uid === file.uid).srcID = srcID;
        }
        this.fileList.find(item => item.uid === file.uid).progreess = 100;
        this.fileList.find(item => item.uid === file.uid).status = 'success';

        // Save to history
        this.saveToHistory(this.fileList.find(item => item.uid === file.uid));
        this.$message({
          type: 'success',
          message: this.truncateFilename(file.name) + ''
        });
        setTimeout(() => {
          this.fileList.find(item => item.uid === file.uid).status = 'done';
        }, 1000);
      } catch (error) {
        this.$message.error(this.truncateFilename(file.name) + '');
        this.fileList.find(item => item.uid === file.uid).status = 'exception';
      }
      //  onUploadComplete  finally 
    },
    saveToHistory(fileItem) {
      try {
        const history = JSON.parse(localStorage.getItem('uploadHistory') || '[]');
        const newRecord = {
          name: fileItem.name,
          url: fileItem.finalURL,
          time: Date.now(),
          type: fileItem.name.split('.').pop().toLowerCase()
        };
        history.push(newRecord);
        localStorage.setItem('uploadHistory', JSON.stringify(history));
      } catch (e) {
        console.error('Failed to save history', e);
      }
    },
    handleError(err, file) {
      this.$message.error(this.truncateFilename(file.name) + '');
      this.fileList.find(item => item.uid === file.uid).status = 'exception';

      // 
      if (this.autoReUpload) {
        this.scheduleAutoRetry();
      }
      //  onUploadComplete  finally 
    },
    handleCopy(file) {
      const status = this.fileList.find(item => item.uid === file.uid).status;
      if (status !== 'done' && status !== 'success') {
        this.$message({
          type: 'warning',
          message: ''
        });
        return;
      }
      if (this.selectedUrlForm === 'url') {
        navigator.clipboard.writeText(file.finalURL);
      } else if (this.selectedUrlForm === 'md') {
        navigator.clipboard.writeText(file.mdURL);
      } else if (this.selectedUrlForm === 'html') {
        navigator.clipboard.writeText(file.htmlURL);
      } else if (this.selectedUrlForm === 'ubb') {
        navigator.clipboard.writeText(file.ubbURL);
      } else {
        navigator.clipboard.writeText(file.finalURL);
      }
      this.$message({
        type: 'success',
        message: ''
      });
    },
    beforeUpload(file) {
      return new Promise((resolve, reject) => {
        // 1. 2.
        const needCustomCompress = file.type.includes('image') && this.customerCompress && file.size / 1024 / 1024 > this.compressBar;
        const isLtLim = file.size / 1024 / 1024 <= 1024 || this.uploadChannel !== 'telegram';
        const pushFileToQueue = (file, serverCompress) => {
          const fileUrl = URL.createObjectURL(file);
          this.fileList.push({
            uid: file.uid,
            name: file.name,
            url: fileUrl,
            finalURL: '',
            mdURL: '',
            htmlURL: '',
            ubbURL: '',
            srcID: '',
            status: 'uploading',
            progreess: 0,
            serverCompress: serverCompress,
            retryCount: 0
          });
          resolve(file);
        };
        if (needCustomCompress) {
          //
          imageConversion.compressAccurately(file, 1024 * this.compressQuality).then(res => {
            //1024MBtelegram
            if (res.size / 1024 / 1024 > 1024 && this.uploadChannel === 'telegram') {
              this.$message.error(file.name + '!');
              reject('');
            }
            this.uploading = true;
            //resfile
            const newFile = new File([res], file.name, {
              type: res.type
            });
            newFile.uid = file.uid;
            const myUploadCount = this.uploadCount++;

            //1. 2.10MB 3.Telegram
            const needServerCompress = this.serverCompress && newFile.size / 1024 / 1024 < 10 && this.uploadChannel === 'telegram';
            if (myUploadCount === 0) {
              pushFileToQueue(newFile, needServerCompress);
            } else {
              setTimeout(() => {
                pushFileToQueue(newFile, needServerCompress);
                this.uploadCount--;
              }, 300 * myUploadCount);
            }
          }).catch(err => {
            this.$message.error(file.name + '!');
            reject(err);
          });
        } else if (isLtLim) {
          this.uploading = true;
          const myUploadCount = this.uploadCount++;

          // 1.Telegram 2. 3.10MB
          const needServerCompress = this.uploadChannel === 'telegram' && this.serverCompress && (file.type.includes('image') ? file.size / 1024 / 1024 < 10 : true);
          if (myUploadCount === 0) {
            pushFileToQueue(file, needServerCompress);
          } else {
            setTimeout(() => {
              pushFileToQueue(file, needServerCompress);
              this.uploadCount--;
            }, 300 * myUploadCount);
          }
        } else {
          this.$message.error(file.name + '!');
          reject('');
        }
      });
    },
    handleProgress(event) {
      this.fileList.find(item => item.uid === event.file.uid).progreess = event.percent;
    },
    copyAll() {
      if (this.selectedUrlForm === 'url') {
        const urls = this.fileList.map(item => {
          if (item.status === 'done' || item.status === 'success') {
            return item.finalURL;
          }
        }).join('\n');
        navigator.clipboard.writeText(urls);
      } else if (this.selectedUrlForm === 'md') {
        const urls = this.fileList.map(item => {
          if (item.status === 'done' || item.status === 'success') {
            return item.mdURL;
          }
        }).join('\n');
        navigator.clipboard.writeText(urls);
      } else if (this.selectedUrlForm === 'html') {
        const urls = this.fileList.map(item => {
          if (item.status === 'done' || item.status === 'success') {
            return item.htmlURL;
          }
        }).join('\n');
        navigator.clipboard.writeText(urls);
      } else if (this.selectedUrlForm === 'ubb') {
        const urls = this.fileList.map(item => {
          if (item.status === 'done' || item.status === 'success') {
            return item.ubbURL;
          }
        }).join('\n');
        navigator.clipboard.writeText(urls);
      } else {
        const urls = this.fileList.map(item => {
          if (item.status === 'done' || item.status === 'success') {
            return item.finalURL;
          }
        }).join('\n');
        navigator.clipboard.writeText(urls);
      }
      this.$message({
        type: 'success',
        message: ''
      });
    },
    clearFileList() {
      if (this.fileList.length > 0) {
        // 
        this.abortControllers.forEach((controller, uid) => {
          controller.abort();
        });
        this.abortControllers.clear();
        // 
        this.uploadQueue = [];
        // 
        this.fileList = [];
        this.$message({
          type: 'success',
          message: ''
        });
      } else {
        this.$message({
          type: 'info',
          message: ''
        });
      }
    },
    clearSuccessList() {
      if (this.uploadSuccessCount > 0) {
        this.fileList = this.fileList.filter(item => item.status !== 'done' && item.status !== 'success');
        this.$message({
          type: 'success',
          message: ''
        });
      } else {
        this.$message({
          type: 'info',
          message: ''
        });
      }
    },
    handlePaste(event) {
      // 
      if (event.target.tagName === 'INPUT' || event.target.tagName === 'TEXTAREA') {
        return;
      }
      const items = event.clipboardData.items;
      if (items.length > 0) {
        this.uploadFromUrl(items);
      }
    },
    handleUploadPasteUrls() {
      // 
      const urls = this.pastedUrls.split('\n');
      // URL
      const validUrls = urls.map(url => url.trim()).filter(url => url !== '').filter(url => /^(https?:\/\/[^\s$.?#].[^\s]*)$/.test(url));

      // 
      if (this.pasteUploadMethod === 'save') {
        // 
        this.uploadFromUrl(validUrls.map(url => {
          return {
            kind: 'string',
            getAsString: callback => {
              callback(url);
            }
          };
        }));
      } else if (this.pasteUploadMethod === 'external') {
        //  external 
        for (let i = 0; i < validUrls.length; i++) {
          const url = validUrls[i];
          const fileName = url.split('/').pop();
          const mdUrl = `![${fileName}](${url})`;
          const htmlUrl = `<img src="${url}" alt="${fileName}" width=100% />`;
          const ubbUrl = `[img]${url}[/img]`;
          //  url  URL 
          const file = new File([], url.split('/').pop(), {
            type: 'text/plain'
          });
          file.uid = Date.now() + i;
          file.url = url;
          this.fileList.push({
            uid: file.uid,
            name: file.name,
            url: url,
            finalURL: url,
            mdURL: mdUrl,
            htmlURL: htmlUrl,
            ubbURL: ubbUrl,
            srcID: url,
            status: 'uploading',
            progreess: 0,
            serverCompress: false,
            uploadChannel: 'external',
            retryCount: 0
          });
          // 
          this.uploadFile({
            file: file,
            onProgress: evt => this.handleProgress(evt),
            onSuccess: (response, file) => this.handleSuccess(response, file),
            onError: (error, file) => this.handleError(error, file)
          });
        }
      }
    },
    uploadFromUrl(items) {
      for (let i = 0; i < items.length; i++) {
        if (items[i].kind === 'file') {
          const file = items[i].getAsFile();
          // 
          file.uid = Date.now() + i;
          //beforeUploadPromise
          const checkResult = this.beforeUpload(file);
          if (checkResult instanceof Promise) {
            checkResult.then(newFile => {
              if (newFile instanceof File) {
                this.uploadFile({
                  file: newFile,
                  onProgress: evt => this.handleProgress(evt),
                  onSuccess: (response, file) => this.handleSuccess(response, file),
                  onError: (error, file) => this.handleError(error, file)
                });
              }
            }).catch(err => {
              console.log(err);
            });
          }
        } else if (items[i].kind === 'string') {
          items[i].getAsString(text => {
            const urlPattern = /^(https?:\/\/[^\s$.?#].[^\s]*)$/;
            let fileName = '';
            if (urlPattern.test(text)) {
              axios.post('/api/fetchRes', {
                url: text
              }, {
                responseType: 'blob'
              }).then(response => {
                const contentType = response.headers['content-type'];
                if (response.status == 200) {
                  // 
                  const disposition = response.headers['content-disposition'];
                  if (disposition) {
                    const filenameRegex = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/;
                    const filenameStarRegex = /filename\*\s*=\s*UTF-8''([^;\n]*)/; //  filename*

                    let matches = filenameRegex.exec(disposition);
                    if (matches != null && matches[1]) {
                      fileName = matches[1].replace(/['"]/g, '');
                      // 
                      try {
                        fileName = decodeURIComponent(fileName);
                      } catch (e) {
                        fileName = '';
                      }
                    }
                    if (fileName === '') {
                      matches = filenameStarRegex.exec(disposition); //  filename*
                      if (matches != null && matches[1]) {
                        fileName = decodeURIComponent(matches[1]);
                      }
                    }
                  }
                  // URL
                  if (fileName === '') {
                    const url = new URL(text);
                    fileName = url.pathname.split('/').pop();
                  }
                  // 
                  if (fileName === '') {
                    // 
                    const url = new URL(text);
                    let extension = url.pathname.split('.').pop();
                    // URLbin
                    if (!extension || extension === '' || extension.length > 10) {
                      extension = 'bin';
                    }
                    fileName = 'PastedFile' + Date.now() + i + '.' + extension;
                  }

                  // 
                  const blob = response.data;
                  const file = new File([blob], fileName, {
                    type: blob.type
                  });
                  file.uid = Date.now() + i;
                  // beforeUploadPromise
                  const checkResult = this.beforeUpload(file);
                  if (checkResult instanceof Promise) {
                    checkResult.then(newFile => {
                      if (newFile instanceof File) {
                        this.uploadFile({
                          file: newFile,
                          onProgress: evt => this.handleProgress(evt),
                          onSuccess: (response, file) => this.handleSuccess(response, file),
                          onError: (error, file) => this.handleError(error, file)
                        });
                      }
                    }).catch(err => {
                      console.log(err);
                    });
                  }
                } else {
                  throw new Error('URL');
                }
              }).catch(error => {
                this.$message({
                  type: 'warning',
                  message: 'URL'
                });
              });
            }
          });
        }
      }
    },
    selectAllText(event) {
      // 
      navigator.clipboard.writeText(event.target.value).then(() => {
        this.$message({
          type: 'success',
          message: ''
        });
      }).catch(() => {
        this.$message({
          type: 'error',
          message: ''
        });
      });
    },
    // 
    isImage(fileName) {
      const imageExtensions = ['jpg', 'jpeg', 'png', 'gif', 'bmp', 'webp', 'svg', 'tiff', 'ico', 'avif', 'heic', 'jfif', 'pjpeg', 'pjp', 'raw', 'cr2', 'nef', 'dng', 'eps', 'ai', 'emf', 'wmf'];
      const extension = fileName.split('.').pop().toLowerCase();
      return imageExtensions.includes(extension);
    },
    // 
    isVideo(fileName) {
      const videoExtensions = ['mp4', 'webm', 'ogg', 'mkv'];
      const extension = fileName.split('.').pop().toLowerCase();
      return videoExtensions.includes(extension);
    },
    handleScroll(event) {
      this.listScrolled = event.scrollTop > 0 && this.fileList.length > 0;
    },
    retryError() {
      if (this.exceptionList.length > 0) {
        this.retryFailedFiles(this.exceptionList);
        this.exceptionList = [];
      } else {
        this.$message({
          type: 'info',
          message: ''
        });
      }
    },
    handleAutoRetryChange(val) {
      this.$message({
        type: val ? 'success' : 'info',
        message: val ? '' : ''
      });

      // 
      if (val && this.exceptionList.length > 0) {
        this.scheduleAutoRetry();
      }
    },
    retryFailedFiles(files) {
      files.forEach(file => {
        const retryCount = file.retryCount || 0;
        if (retryCount < this.maxRetryCount) {
          file.retryCount = retryCount + 1;
          this.uploadFile({
            file: file.file,
            onProgress: evt => this.handleProgress(evt),
            onSuccess: (response, file) => this.handleSuccess(response, file),
            onError: (error, file) => this.handleError(error, file)
          });
        } else {
          this.$message({
            type: 'warning',
            message: `${file.name} (${this.maxRetryCount})`
          });
        }
      });
    },
    scheduleAutoRetry() {
      if (this.retryTimer) {
        clearTimeout(this.retryTimer);
      }
      this.retryTimer = setTimeout(() => {
        if (this.autoReUpload && this.exceptionList.length > 0) {
          const filesToRetry = [...this.exceptionList];
          this.exceptionList = [];
          this.retryFailedFiles(filesToRetry);
        }
      }, this.retryDelay);
    },
    // HuggingFace  CF Workers 
    //  SHA256   S3  URL   S3  
    async uploadToHuggingFaceDirect(file) {
      const fileItem = this.fileList.find(item => item.uid === file.file.uid);
      if (!fileItem) return;

      //  AbortController 
      const abortController = new AbortController();
      this.abortControllers.set(file.file.uid, abortController);
      try {
        console.log('=== HuggingFace Direct Upload ===');
        console.log('File:', file.file.name, 'Size:', file.file.size);

        // 1.  SHA256
        file.onProgress({
          percent: 5,
          file: file.file
        });
        console.log('Computing SHA256...');
        const sha256 = await this.computeSha256(file.file);
        console.log('SHA256:', sha256);

        // 
        if (abortController.signal.aborted) {
          console.log('HuggingFace :', file.file.name);
          return;
        }

        // 2. 512base64
        const sampleBytes = new Uint8Array(await file.file.slice(0, 512).arrayBuffer());
        const fileSample = btoa(String.fromCharCode(...sampleBytes));

        // 3.  LFS  URL
        file.onProgress({
          percent: 10,
          file: file.file
        });
        console.log('Getting LFS upload URL...');
        const uploadInfoRes = await axios({
          url: '/api/huggingface/getUploadUrl',
          method: 'post',
          data: {
            fileSize: file.file.size,
            fileName: file.file.name,
            sha256,
            fileSample
          },
          withAuthCode: true,
          signal: abortController.signal
        });
        if (!uploadInfoRes.data.success) {
          throw new Error(uploadInfoRes.data.error || 'Failed to get upload URL');
        }
        const uploadInfo = uploadInfoRes.data;
        console.log('Upload info:', uploadInfo);

        // 
        if (uploadInfo.alreadyExists) {
          console.log('File already exists in LFS, skipping upload');
          file.onProgress({
            percent: 90,
            file: file.file
          });
        } else if (uploadInfo.needsLfs && uploadInfo.uploadAction) {
          // 4.  S3
          const {
            href,
            header
          } = uploadInfo.uploadAction;
          if (header?.chunk_size) {
            // 
            await this.uploadToHuggingFaceMultipart(file, uploadInfo, abortController);
          } else {
            // 
            console.log('Uploading to S3 (basic)...');
            const uploadRes = await fetch(href, {
              method: 'PUT',
              headers: header || {},
              body: file.file,
              signal: abortController.signal
            });
            if (!uploadRes.ok) {
              const error = await uploadRes.text();
              throw new Error(`S3 upload failed: ${uploadRes.status} - ${error}`);
            }
            console.log('S3 upload complete');
          }
        }

        // 
        if (abortController.signal.aborted) {
          console.log('HuggingFace :', file.file.name);
          return;
        }

        // 5. 
        file.onProgress({
          percent: 95,
          file: file.file
        });
        console.log('Committing file...');
        const commitRes = await axios({
          url: '/api/huggingface/commitUpload',
          method: 'post',
          data: {
            fullId: uploadInfo.fullId,
            filePath: uploadInfo.filePath,
            sha256,
            fileSize: file.file.size,
            fileName: file.file.name,
            channelName: uploadInfo.channelName
          },
          withAuthCode: true,
          signal: abortController.signal
        });
        if (!commitRes.data.success) {
          throw new Error(commitRes.data.error || 'Failed to commit file');
        }
        console.log('Upload complete:', commitRes.data);
        //  handleSuccess 
        const formattedResponse = {
          data: [{
            src: commitRes.data.src
          }]
        };
        file.onSuccess(formattedResponse, file.file);
      } catch (err) {
        // 
        if (err.name === 'CanceledError' || err.code === 'ERR_CANCELED' || err.name === 'AbortError') {
          console.log('HuggingFace :', file.file.name);
          return;
        }
        console.error('HuggingFace direct upload error:', err);
        this.exceptionList.push(file);
        file.onError(err, file.file);
      } finally {
        //  AbortController
        this.abortControllers.delete(file.file.uid);
        // 
        this.onUploadComplete();
      }
    },
    // HuggingFace  S3
    async uploadToHuggingFaceMultipart(file, uploadInfo, abortController) {
      const {
        uploadAction
      } = uploadInfo;
      const {
        href: completionUrl,
        header
      } = uploadAction;
      const chunkSize = parseInt(header.chunk_size);

      //  URL
      const parts = Object.keys(header).filter(key => /^[0-9]+$/.test(key));
      console.log(`Multipart upload: ${parts.length} parts, chunk size: ${chunkSize}`);
      const completeParts = [];
      const totalParts = parts.length;
      for (const part of parts) {
        // 
        if (abortController && abortController.signal.aborted) {
          console.log('HuggingFace ');
          throw new DOMException('Upload cancelled', 'AbortError');
        }
        const index = parseInt(part) - 1;
        const start = index * chunkSize;
        const end = Math.min(start + chunkSize, file.file.size);
        const chunk = file.file.slice(start, end);
        console.log(`Uploading part ${part}/${totalParts}`);
        const response = await fetch(header[part], {
          method: 'PUT',
          body: chunk,
          signal: abortController ? abortController.signal : undefined
        });
        if (!response.ok) {
          throw new Error(`Failed to upload part ${part}: ${response.status}`);
        }
        const etag = response.headers.get('ETag');
        if (!etag) {
          throw new Error(`No ETag for part ${part}`);
        }
        completeParts.push({
          partNumber: parseInt(part),
          etag
        });

        // 10% - 90%
        const progress = 10 + Math.round(parseInt(part) / totalParts * 80);
        file.onProgress({
          percent: progress,
          file: file.file
        });
      }

      // 
      if (abortController && abortController.signal.aborted) {
        console.log('HuggingFace ');
        throw new DOMException('Upload cancelled', 'AbortError');
      }

      // 
      console.log('Completing multipart upload...');
      const completeResponse = await fetch(completionUrl, {
        method: 'POST',
        headers: {
          'Accept': 'application/vnd.git-lfs+json',
          'Content-Type': 'application/vnd.git-lfs+json'
        },
        body: JSON.stringify({
          oid: uploadInfo.oid,
          parts: completeParts
        }),
        signal: abortController ? abortController.signal : undefined
      });
      if (!completeResponse.ok) {
        const error = await completeResponse.text();
        throw new Error(`Multipart complete failed: ${completeResponse.status} - ${error}`);
      }
      console.log('Multipart upload complete');
    },
    //  SHA256  HuggingFace 
    // 
    async computeSha256(file) {
      //  JavaScript  SHA256
      // 
      const sha256 = this.createSha256();
      const CHUNK_SIZE = 4 * 1024 * 1024; // 4MB chunks - 
      let offset = 0;
      while (offset < file.size) {
        const chunk = file.slice(offset, Math.min(offset + CHUNK_SIZE, file.size));
        const buffer = await chunk.arrayBuffer();
        sha256.update(new Uint8Array(buffer));
        offset += CHUNK_SIZE;

        //  20MB 
        if (offset % (20 * 1024 * 1024) < CHUNK_SIZE) {
          console.log(`SHA256 progress: ${Math.min(100, Math.round(offset / file.size * 100))}%`);
        }
      }
      return sha256.digest();
    },
    //  SHA256  JavaScript 
    createSha256() {
      // SHA256 
      const K = new Uint32Array([0x428a2f98, 0x71374491, 0xb5c0fbcf, 0xe9b5dba5, 0x3956c25b, 0x59f111f1, 0x923f82a4, 0xab1c5ed5, 0xd807aa98, 0x12835b01, 0x243185be, 0x550c7dc3, 0x72be5d74, 0x80deb1fe, 0x9bdc06a7, 0xc19bf174, 0xe49b69c1, 0xefbe4786, 0x0fc19dc6, 0x240ca1cc, 0x2de92c6f, 0x4a7484aa, 0x5cb0a9dc, 0x76f988da, 0x983e5152, 0xa831c66d, 0xb00327c8, 0xbf597fc7, 0xc6e00bf3, 0xd5a79147, 0x06ca6351, 0x14292967, 0x27b70a85, 0x2e1b2138, 0x4d2c6dfc, 0x53380d13, 0x650a7354, 0x766a0abb, 0x81c2c92e, 0x92722c85, 0xa2bfe8a1, 0xa81a664b, 0xc24b8b70, 0xc76c51a3, 0xd192e819, 0xd6990624, 0xf40e3585, 0x106aa070, 0x19a4c116, 0x1e376c08, 0x2748774c, 0x34b0bcb5, 0x391c0cb3, 0x4ed8aa4a, 0x5b9cca4f, 0x682e6ff3, 0x748f82ee, 0x78a5636f, 0x84c87814, 0x8cc70208, 0x90befffa, 0xa4506ceb, 0xbef9a3f7, 0xc67178f2]);
      let H = new Uint32Array([0x6a09e667, 0xbb67ae85, 0x3c6ef372, 0xa54ff53a, 0x510e527f, 0x9b05688c, 0x1f83d9ab, 0x5be0cd19]);
      let buffer = new Uint8Array(64);
      let bufferLength = 0;
      let totalLength = 0;
      const rotr = (x, n) => x >>> n | x << 32 - n;
      const processBlock = block => {
        const W = new Uint32Array(64);
        for (let i = 0; i < 16; i++) {
          W[i] = block[i * 4] << 24 | block[i * 4 + 1] << 16 | block[i * 4 + 2] << 8 | block[i * 4 + 3];
        }
        for (let i = 16; i < 64; i++) {
          const s0 = rotr(W[i - 15], 7) ^ rotr(W[i - 15], 18) ^ W[i - 15] >>> 3;
          const s1 = rotr(W[i - 2], 17) ^ rotr(W[i - 2], 19) ^ W[i - 2] >>> 10;
          W[i] = W[i - 16] + s0 + W[i - 7] + s1 >>> 0;
        }
        let [a, b, c, d, e, f, g, h] = H;
        for (let i = 0; i < 64; i++) {
          const S1 = rotr(e, 6) ^ rotr(e, 11) ^ rotr(e, 25);
          const ch = e & f ^ ~e & g;
          const temp1 = h + S1 + ch + K[i] + W[i] >>> 0;
          const S0 = rotr(a, 2) ^ rotr(a, 13) ^ rotr(a, 22);
          const maj = a & b ^ a & c ^ b & c;
          const temp2 = S0 + maj >>> 0;
          h = g;
          g = f;
          f = e;
          e = d + temp1 >>> 0;
          d = c;
          c = b;
          b = a;
          a = temp1 + temp2 >>> 0;
        }
        H[0] = H[0] + a >>> 0;
        H[1] = H[1] + b >>> 0;
        H[2] = H[2] + c >>> 0;
        H[3] = H[3] + d >>> 0;
        H[4] = H[4] + e >>> 0;
        H[5] = H[5] + f >>> 0;
        H[6] = H[6] + g >>> 0;
        H[7] = H[7] + h >>> 0;
      };
      return {
        update(data) {
          totalLength += data.length;
          let offset = 0;
          if (bufferLength > 0) {
            const needed = 64 - bufferLength;
            const toCopy = Math.min(needed, data.length);
            buffer.set(data.subarray(0, toCopy), bufferLength);
            bufferLength += toCopy;
            offset = toCopy;
            if (bufferLength === 64) {
              processBlock(buffer);
              bufferLength = 0;
            }
          }
          while (offset + 64 <= data.length) {
            processBlock(data.subarray(offset, offset + 64));
            offset += 64;
          }
          if (offset < data.length) {
            buffer.set(data.subarray(offset), 0);
            bufferLength = data.length - offset;
          }
        },
        digest() {
          const bitLength = totalLength * 8;

          // Padding
          buffer[bufferLength++] = 0x80;
          if (bufferLength > 56) {
            buffer.fill(0, bufferLength, 64);
            processBlock(buffer);
            bufferLength = 0;
          }
          buffer.fill(0, bufferLength, 56);

          // Length in bits (big-endian, 64-bit)
          const view = new DataView(buffer.buffer);
          view.setUint32(56, Math.floor(bitLength / 0x100000000), false);
          view.setUint32(60, bitLength >>> 0, false);
          processBlock(buffer);

          // Convert to hex
          let hex = '';
          for (let i = 0; i < 8; i++) {
            hex += H[i].toString(16).padStart(8, '0');
          }
          return hex;
        }
      };
    }
  },
  beforeDestroy() {
    // 
    if (this.retryTimer) {
      clearTimeout(this.retryTimer);
    }
  }
};